import axios from "axios";

export default {
    name: 'GouacheIndex',

    data() {
        return {
            gouaches: []
        }
    },

    methods: {
        getGouaches() {
            axios.get('/static/gouaches.json')
            .then(result => {
                this.gouaches = result.data;
            })
            .catch((error) => {
                throw error.response.data;
            });
        }
    },

    created() {
        this.getGouaches();
    }
}